.line-through{text-decoration:line-through;user-select:none}.page h3{font-size:2em;font-family:Roboto Slab,serif;margin-bottom:0}.page form{display:grid;grid-template-columns:400px 1fr;grid-gap:16px}.page label{grid-column:1 / 2;font-size:1.4em;font-family:Roboto Mono,monospace;margin-bottom:0}.page input{grid-column:2 / 3;font-size:1.4em;font-family:Roboto Mono,monospace;margin-bottom:0}.page input:not([type='submit']){padding:10px}.page input:disabled{opacity:0.5}.page textarea{grid-column:2 / 3;min-height:150px;padding:10px;font-size:1.4em;font-family:Roboto Mono,monospace;margin-bottom:0;line-height:2}.page input[type='submit']{width:200px;background:none;margin:25px 0 0 0}.page input[type='submit']:hover{background:#dedede}.page .error{font-size:1.4em;font-family:Roboto Mono,monospace;margin-bottom:1.78571em;color:red}.page .thumbnail{margin:0 0 20px 0}
