.line-through{text-decoration:line-through;user-select:none}.navHeader{display:grid;grid-template-columns:50% 50%;grid-gap:0}.navLeftColumn{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:start;-ms-flex-pack:start;justify-content:flex-start}.navRightColumn{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:end;-ms-flex-pack:end;justify-content:flex-end}.navRightColumn>p{padding:0 20px 0 0}.navRightColumn>a:first-of-type p{padding:0 20px 0 0}
