.line-through{text-decoration:line-through;user-select:none}.user form{display:grid;grid-template-columns:400px 1fr;grid-gap:16px}.user label{grid-column:1 / 2;font-size:1.4em;font-family:Roboto Mono,monospace;margin-bottom:0}.user input{grid-column:2 / 3;font-size:1.4em;font-family:Roboto Mono,monospace;margin-bottom:0}.user input:not([type='submit']){padding:10px}.user input:disabled{opacity:0.5}.user textarea{grid-column:2 / 3;min-height:150px;padding:10px;font-size:1.4em;font-family:Roboto Mono,monospace;margin-bottom:0}.user input[type='submit']{width:200px;background:none;margin:25px 0 0 0}.user input[type='submit']:hover{background:#dedede}.user .error{font-size:1.4em;font-family:Roboto Mono,monospace;margin-bottom:1.78571em;color:red}.user .success{color:green}
