.line-through{text-decoration:line-through;user-select:none}.addPage form{display:grid;grid-template-columns:400px 1fr;grid-gap:16px}.addPage label{grid-column:1 / 2;font-size:1.4em;font-family:Roboto Mono,monospace;margin-bottom:0}.addPage input{grid-column:2 / 3;font-size:1.4em;font-family:Roboto Mono,monospace;margin-bottom:0}.addPage input:not([type='submit']){padding:10px}.addPage textarea{grid-column:2 / 3;min-height:150px;padding:10px;font-size:1.4em;font-family:Roboto Mono,monospace;margin-bottom:0;line-height:2}.addPage input[type='submit']{width:200px;background:none}.addPage input[type='submit']:hover{background:#dedede}.addPage .error{font-size:1.4em;font-family:Roboto Mono,monospace;margin-bottom:1.78571em;color:red}
