.line-through{text-decoration:line-through;user-select:none}.comment h3{font-size:2em;font-family:Roboto Slab,serif;margin-bottom:0}.comment form{display:grid;grid-template-columns:400px 1fr;grid-gap:16px}.comment label{grid-column:1 / 2;font-size:1.4em;font-family:Roboto Mono,monospace;margin-bottom:0}.comment input{grid-column:2 / 3;font-size:1.4em;font-family:Roboto Mono,monospace;margin-bottom:0}.comment input:not([type='submit']){padding:10px}.comment input:disabled{opacity:0.5}.comment textarea{grid-column:2 / 3;min-height:150px;padding:10px;font-size:1.4em;font-family:Roboto Mono,monospace;margin-bottom:0;line-height:2}.comment input[type='submit']{width:200px;background:none;margin:25px 0 0 0}.comment input[type='submit']:hover{background:#dedede}.comment .error{font-size:1.4em;font-family:Roboto Mono,monospace;margin-bottom:1.78571em;color:red}.comment .thumbnail{margin:0 0 20px 0}
