.line-through{text-decoration:line-through;user-select:none}:global .modal-mask{position:fixed;z-index:9998;top:0;left:0;width:100%;height:100%;background-color:rgba(0,0,0,0.5);display:table;-webkit-transition:opacity 0.3s ease;transition:opacity 0.3s ease}:global .modal-wrapper{display:table-cell;vertical-align:middle;padding:15px}:global .modal-container{width:300px;margin:0 auto;padding:20px 30px;background-color:#fff;border-radius:2px;-webkit-box-shadow:0 2px 8px rgba(0,0,0,0.33);box-shadow:0 2px 8px rgba(0,0,0,0.33);-webkit-transition:all 0.3s ease;transition:all 0.3s ease}:global .modal-header h3{line-height:1.3em}:global .modal-body{margin:20px 0}:global .modal-footer{display:grid;grid-template-columns:calc(50% - 15px) calc(50% - 15px);grid-gap:30px}:global .modal-footer button{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;font-size:1.4em;font-family:Roboto Mono,monospace;margin-bottom:0;padding:5px 0;background:#fff;-webkit-box-shadow:none;box-shadow:none;border:1px solid #dedede}:global .modal-footer button:hover{background:#dedede}:global .modal-enter{opacity:0}:global .modal-leave-active{opacity:0}:global .modal-enter .modal-container{-webkit-transform:scale(1.1);transform:scale(1.1)}:global .modal-leave-active .modal-container{-webkit-transform:scale(1.1);transform:scale(1.1)}
